<template>
        <div class="item-content">
            <div class="item-inner item-cell">
                <div class="item-row">
                    <div class="item-cell dice_number text-align-center">{{ random }}</div>
                    <div class="item-cell">Min ({{min+1}})<f7-range :min="0" :max="100" :step="1" :value="min" @range:change="(value) => {min = value}"></f7-range></div>
                    <div class="item-cell">Max ({{max}})<f7-range :min="0" :max="100" :step="1" :value="max" @range:change="(value) => {max = value}"></f7-range></div>
                    <f7-button center big fill class="dice_roll_button" @click="randomNumber()">Roll</f7-button>
                </div>
            </div>
        </div>

</template>


<script>
    import F7Button from "../../node_modules/framework7-vue/src/components/button";

    export default {
        components: {F7Button},
        name: 'dice',

        data() {
            return {
                min:0,
                max:6,
                random: 1,
            }
        },

        methods: {
            randomNumber: function () {
                this.random = Math.ceil(Math.random() * (this.max - this.min) + this.min)
            },
        }
    };
</script>


