<template>
  <f7-page class="no-navbar">
        <f7-block class="ldnpk_flexbox_menu noPM mainmenu">
            <!-- Link to other pages -->


            <f7-link href="#" class="tab-link ldnpk_flexbox_menu_item mainmenu_logo">
                <div class="ldnpk_mainmenu_logo"></div>
            </f7-link>

            <f7-link href="#classes-tab" class="tab-link ldnpk_flexbox_menu_item mainmenu_classes">
                <div class="ldnpk_mainmenu_image"></div>
                <div class="ldnpk_mainmenu_title">Classes</div>
            </f7-link>

            <f7-link href="#news-tab" class="tab-link ldnpk_flexbox_menu_item mainmenu_news">
                <div class="ldnpk_mainmenu_image"><img src=""></div>
                <div class="ldnpk_mainmenu_title">News</div>
            </f7-link>

            <f7-link href="/tools/"class="ldnpk_flexbox_menu_item mainmenu_tools">
                <div class="ldnpk_mainmenu_image"><img src=""></div>
                <div class="ldnpk_mainmenu_title">Tools</div>
            </f7-link>

            <f7-link href="/settings/"class="ldnpk_flexbox_menu_item mainmenu_settings">
                <div class="ldnpk_mainmenu_image"><img src=""></div>
                <div class="ldnpk_mainmenu_title">Settings</div>
            </f7-link>

            <f7-link v-show="this.coach" href="/coaches/" class="ldnpk_flexbox_menu_item mainmenu_coaches">
                <div class="ldnpk_mainmenu_image"><img src=""></div>
                <div class="ldnpk_mainmenu_title">Coach</div>
            </f7-link>
        </f7-block>
  </f7-page>
</template>


<script>
    import F7Block from "../../node_modules/framework7-vue/src/components/block";
export default {
    components: {F7Block},
    name: 'Home',

    data() {
        return {
            coach: null,
        };
    },

    mounted: function(){
        this.coach = window.localStorage.getItem('coach');
    }

};
</script>



