<template>
    <f7-page>
        <f7-navbar sliding title="Stopwatch" back-link="Back" color="ldnpk_theme">
            <f7-nav-right><f7-link icon="icon-bars" popup-open="#help-popup">Help</f7-link></f7-nav-right>
        </f7-navbar>

        <div class="list links-list">
            <ul class="stopwatches">
                <stopwatch v-for="n in range" :key="n" class="color-ldnpk_theme"></stopwatch>
            </ul>
        </div>

        <div class="block">
            <f7-button center big fill color="pink" @click="addStopwatch">+</f7-button>
        </div>

        <f7-popup id="help-popup">
            <f7-view class="popup-view">
                <f7-page>
                    <div class="navbar">
                        <div class="navbar-inner">
                            <div class="left margin-left"><i class="f7-icons ios-only link-color">help</i><i class="material-icons md-only">help_outline</i></div>
                            <div class="title">Stopwatches</div>
                            <div class="right"><a class="link popup-close" href="#">Close</a></div>
                        </div>
                    </div>
                    <div class="page-content block">
                        <h1>Stopwatch Help</h1>
                        <p>The stopwatch can be a little confusing at first, but perfectly logical.</p>
                        <p>Firstly, remember that the stopwatch is ALWAYS running.
                            When you press the pause/play button, you are not stopping it at all.</p>
                        <p>You can give labels to each stopwatch in the input box and add as many stopwatches as you like.</p>
                        <H2>Play / Pause</H2>
                        <p>The idea is that when you press the pause button, you get a snapshot of that current time,
                            but the stopwatch is still running in the background. When you press the play button again,
                            The snapshot time is replaced by the still-running stopwatch.</p>
                        <h2>Reset</h2>
                        <p>The reset button is used to put the stopwatch back to 0. Press this when you want to start timing
                            something.</p>
                        <hr/>
                        <p>This is good for timing multiple people doing laps or when there is a multi-time element to an exercise.</p>

                    </div>
                </f7-page>
            </f7-view>
        </f7-popup>
    </f7-page>
</template>


<script>
    import stopwatch from "./tool_stopwatch.vue";

    export default {
        components: {stopwatch},
        name: 'stopwatches',

        data() {
            return {
                range:1
            }
        },

        methods: {
            addStopwatch: function(){
                this.range += 1;
            },
        }
    };
</script>


