<template>
    <f7-page>
        <f7-navbar title="Menu" back-link="Back" ></f7-navbar>

        <f7-block class="color-ldnpk_theme"><h1>Coach Class Plan</h1></f7-block>
        <div class="tabs">
            <div class="tab tab-active text-align-center" id="coaches-tab">
                <div v-show="loaderror">ERROR</div>
                <div class="loader" v-show="!coaches.loaded">
                    <p>Loading Class Plan from LondonParkour.com</p>
                    <p>Please wait.</p>
                    <f7-preloader></f7-preloader>
                </div>

                <iframe class="coaches_iframe" :src="coaches.src" ref="frame" @load="coachesload" v-show="coaches.loaded" width="100%" style="height: 100vh;" frameborder="0"></iframe>
            </div>
        </div>

    </f7-page>
</template>


<script>
    import F7Preloader from "../../node_modules/framework7-vue/src/components/preloader";
    import F7Link from "../../node_modules/framework7-vue/src/components/link";
    import F7Block from "../../node_modules/framework7-vue/src/components/block";

    export default {
        components: {
            F7Block,
            F7Link,
            F7Preloader},
        name: 'CoachesClassLog',

        data() {
            return {
                coaches: {
                    src: 'http://londonparkour.com/mobile/coach-class-plan?cmp_bypass=ldnpk',
                    loaded: false
                },
                timeout: 0,
                loaderror: false
            }

        },

        methods: {
            coachesload: function(){
                this.coaches.loaded = true;
            },
            goBack: function(){
                this.$f7.tab.show('#main-tab');
            },
            showError: function(){
                this.loaderror = true;
            }
        }

    };


</script>


