<template>
    <f7-page>
        <f7-navbar sliding color="ldnpk_theme">
            <f7-nav-left>
                <f7-link icon="icon-back" v-on:click="goBack()"> Back</f7-link>
            </f7-nav-left>
        </f7-navbar>

        <f7-subnavbar color="ldnpk_theme">
            <f7-segmented >
                <f7-button tab-link tab-link-active href="#class-tab">Classes</f7-button>
                <f7-button tab-link href="#pt-tab">Personal Training</f7-button>
            </f7-segmented>
        </f7-subnavbar>

        <f7-block><h1>Classes & P.T.</h1></f7-block>
        <div class="tabs">
            <div class="tab tab-active text-align-center" id="class-tab">
                <div v-show="loaderror">ERROR</div>
                <div class="loader" v-show="!classes.loaded">
                    <p>Loading Upcoming Classes from LondonParkour.com</p>
                    <f7-preloader></f7-preloader>
                </div>
                <iframe class="classes_iframe" :src="classes.src" ref="frame" @load="classload" v-show="classes.loaded" width="100%" style="height: 100vh;" frameborder="0"></iframe>
            </div>

            <div class="tab text-align-center" id="pt-tab">
                <div class="loader" v-show="!pt.loaded">
                    <p>Loading Private Tuition from LondonParkour.com</p>
                    <f7-preloader></f7-preloader>
                </div>
                <iframe class="classes_iframe" :src="pt.src" ref="frame" @load="ptload" v-show="pt.loaded" width="100%" style="height: 100vh;" frameborder="0"></iframe>
            </div>
        </div>

    </f7-page>
</template>


<script>
    import F7Preloader from "../../node_modules/framework7-vue/src/components/preloader";
    import F7Link from "../../node_modules/framework7-vue/src/components/link";

    export default {
        components: {
            F7Link,
            F7Preloader},
        name: 'Classes',

        data() {
            return {
                classes: {
                    src: 'http://londonparkour.com/mobile/upcoming?cmp_bypass=ldnpk',
                    loaded: false
                },
                pt: {
                    src: 'http://londonparkour.com/mobile/private-tuition?cmp_bypass=ldnpk',
                    loaded: false
                },
                timeout: 0,
                loaderror: false
            }

        },

        created: function () {
            // `this` points to the vm instance
        },

        methods: {
            classload: function(){
                this.classes.loaded = true;
            },
            ptload: function(){
                this.pt.loaded = true;
            },
            goBack: function(){
                this.$f7.tab.show('#main-tab');
            },
            showError: function(){
                this.loaderror = true;
            }
        }

    };


</script>


