<template>
    <f7-page>
        <f7-navbar sliding title="Dice" back-link="Back" color="ldnpk_theme">
            <f7-nav-right><f7-link icon="icon-bars" popup-open="#help-popup">Help</f7-link></f7-nav-right>
        </f7-navbar>

        <f7-list class="dices">
            <dice v-for="n in range" :key="n" class="color-ldnpk_theme"></dice>
        </f7-list>

        <div class="block">
            <f7-button center big fill color="pink" @click="addDice">+</f7-button>
        </div>

        <f7-popup id="help-popup">
            <f7-view class="popup-view">
                <f7-page>
                    <div class="navbar">
                        <div class="navbar-inner">
                            <div class="left margin-left"><i class="f7-icons ios-only link-color">help</i><i class="material-icons md-only">help_outline</i></div>
                            <div class="title">Dice</div>
                            <div class="right"><a class="link popup-close" href="#">Close</a></div>
                        </div>
                    </div>
                    <div class="page-content block">
                        <h1>Dice Help</h1>
                        <p>The dice tool is a simple random number generator.</p>
                        <p>You are able to set the minimum and maximum bounds of the random number. By default, each one is the same as
                            a regular dice. 1 to 6. </p>
                        <p>Say you wanted a random number from 1 to 20, simply move the 'Max' slider up to 20 and
                            press the 'Roll' button.</p>
                        <p>You are able to set the lower-limit number also, so if you wanted 50 to 99, you can do that
                            by adusting both the 'Min' and 'Max' sliders before pressing the 'Roll' button.</p>
                        <p>You are able to add multiple dice, each with different lower and upper limits.</p>
                        <hr/>
                        <p>A great way of including a random element into training. Use the dice to determine anything from:</p>
                        <ul>
                            <li>Number of repetitions</li>
                            <li>Number of Sets</li>
                            <li>Length of exercise</li>
                            <li>Type of exercise by substituting a number for an exercise</li>
                            <li>Many, many, other things variables.</li>
                        </ul>
                    </div>
                </f7-page>
            </f7-view>
        </f7-popup>

    </f7-page>
</template>


<script>
    import dice from "./tool_dice.vue";

    export default {
        components: {dice},
        name: 'dices',

        data() {
            return {
                range:1
            }
        },

        methods: {
            addDice: function(){
                this.range += 1;
            },
        }
    };
</script>


