import HomePage from './pages/home.vue';
import ClassesPage from './pages/classes.vue';
import ToolsPage from './pages/tools.vue';
import ToolsCountersPage from './pages/tool_counters.vue';
import ToolsDicesPage from './pages/tool_dices.vue';
import ToolsStopwatchesPage from './pages/tool_stopwatches.vue';
import ToolsCountdownsPage from './pages/tool_countdowns.vue';
import ToolsMultitoolPage from './pages/tool_multitool.vue';
import SettingsPage from './pages/settings.vue';
import NewsPage from './pages/news.vue';
import CoachesPage from './pages/coaches.vue';
import CoachClassLogPage from './pages/coach_class_log.vue';
import CoachClassPlanPage from './pages/coach_class_plan.vue';
import CoachClassAnalyticsPage from './pages/coach_class_analytics.vue';
import CoachAccidentReportPage from './pages/coach_accident_report.vue';
import CoachQuickStripePage from './pages/coach_quick_payment.vue';

import NotFoundPage from './pages/not-found.vue';

export default [
    {
        path: '/',
        component: HomePage
    },
    {
        path: '/coaches/',
        component: CoachesPage
    },
    {
        path: '/coaches_class_log/',
        component: CoachClassLogPage
    },
    {
        path: '/coaches_class_plan/',
        component: CoachClassPlanPage
    },
    {
        path: '/coaches_class_analytics/',
        component: CoachClassAnalyticsPage
    },
    {
        path: '/coaches_accident_report/',
        component: CoachAccidentReportPage
    },
    {
        path: '/coaches_quick_stripe/',
        component: CoachQuickStripePage
    },
    {
        path: '/classes/',
        component: ClassesPage
    },
    {
        path: '/tools/',
        component: ToolsPage
    },
    {
        path: '/counters/',
        component: ToolsCountersPage
    },
    {
        path: '/stopwatches/',
        component: ToolsStopwatchesPage
    },
    {
        path: '/dices/',
        component: ToolsDicesPage
    },
    {
        path: '/countdowns/',
        component: ToolsCountdownsPage
    },
    {
        path: '/multitool/',
        component: ToolsMultitoolPage
    },
    {
        path: '/settings/',
        component: SettingsPage
    },
    {
        path: '/news/',
        component: NewsPage
    },
    {
        path: '(.*)',
        component: NotFoundPage
    }
];



