<template>
    <f7-page>
        <f7-navbar sliding color="ldnpk_theme">
            <f7-nav-left>
                <f7-link icon="icon-back" v-on:click="goBack()">Back</f7-link>
            </f7-nav-left>
        </f7-navbar>

        <f7-block><h1>Latest News</h1></f7-block>
        <div class="tabs">
            <div class="tab tab-active text-align-center" id="news-tab">
                <div v-show="loaderror">ERROR</div>
                <div class="loader" v-show="!news.loaded">
                    <p>Loading News from LondonParkour.com</p>
                    <p>Also check Instagram.com/london_parkour.</p>
                    <f7-preloader></f7-preloader>
                </div>

                <iframe class="news_iframe" :src="news.src" ref="frame" @load="newsload" v-show="news.loaded" width="100%" style="height: 100vh;" frameborder="0"></iframe>
            </div>
        </div>

    </f7-page>
</template>


<script>
    import F7Preloader from "../../node_modules/framework7-vue/src/components/preloader";
    import F7Link from "../../node_modules/framework7-vue/src/components/link";
    import F7Block from "../../node_modules/framework7-vue/src/components/block";

    export default {
        components: {
            F7Block,
            F7Link,
            F7Preloader},
        name: 'News',

        data() {
            return {
                news: {
                    src: 'http://londonparkour.com/mobile/news?cmp_bypass=ldnpk',
                    loaded: false
                },
                timeout: 0,
                loaderror: false
            }

        },

        methods: {
            newsload: function(){
                this.news.loaded = true;
            },
            goBack: function(){
                this.$f7.tab.show('#main-tab');
            },
            showError: function(){
                this.loaderror = true;
            }
        }

    };


</script>


