<template>
    <f7-page>
        <f7-navbar sliding title="MultiTool" back-link="Back" color="ldnpk_theme">
            <f7-nav-right><f7-link icon="icon-bars" popup-open="#help-popup">Help</f7-link></f7-nav-right>
        </f7-navbar>

        <div class="list links-list" >
            <ul class="color-ldnpk_theme">
                <component v-for="(tool, index) in toollist" class="color-ldnpk_theme"  :is="tool.type" :key="tool.index"></component>
            </ul>
        </div>

        <div class="block">
            <f7-row>
                <f7-button center fill color="blue" @click="addStopwatch">+StopWatch</f7-button>
                <f7-button center fill color="pink" @click="addDice">+Dice</f7-button>
                <f7-button center fill color="green" @click="addCounter">+Tally</f7-button>
                <f7-button center fill color="black" @click="undo">-Undo</f7-button>
            </f7-row>
        </div>


        <f7-popup id="help-popup">
            <f7-view class="popup-view">
                <f7-page>
                    <div class="navbar">
                        <div class="navbar-inner">
                            <div class="left margin-left"><i class="f7-icons ios-only link-color">help</i><i class="material-icons md-only">help_outline</i></div>
                            <div class="title">Multitool</div>
                            <div class="right"><a class="link popup-close" href="#">Close</a></div>
                        </div>
                    </div>
                    <div class="page-content block">
                        <h1>MultiTool Help</h1>
                        <p>The multitool combines the Stopwatch, Dice and Counter all into one page.
                            This allows you to create a more versatile training session.</p>
                        <p>The '-undo' button will remove the last tool added.</p>
                    </div>
                </f7-page>
            </f7-view>
        </f7-popup>

    </f7-page>
</template>


<script>
    import stopwatch from "./tool_stopwatch.vue";
    import dice from "./tool_dice.vue";
    import counter from "./tool_counter.vue";
    import F7Row from "../../node_modules/framework7-vue/src/components/row";

    export default {
        components: {
            F7Row,
            stopwatch, dice, counter},
        name: 'multitool',

        data() {
            return {
                toollist: []
            }
        },

        methods: {
            addStopwatch: function(){
                this.toollist.push({ type: 'stopwatch' })
            },
            addDice: function(){
                this.toollist.push({ type: 'dice' })
            },
            addCounter: function(){
                this.toollist.push({ type: 'counter' })
            },
            undo: function(){
                this.toollist.pop();
            },
        }
    };
</script>


