<template>
    <div class="block inset">
        <span class="digit">{{ days }} Days | {{ hours }} Hrs | {{ minutes }} Mins | {{ seconds }} Secs</span>
    </div>
</template>


<script>
    import F7Button from "../../node_modules/framework7-vue/src/components/button";

    export default {
        components: {F7Button},
        name: 'countdown',

        props : {
            date : {
                date: null
            }
        },

        data() {
            return {
                now: Math.trunc((new Date()).getTime() / 1000),
                event: this.date
            }
        },

        mounted () {
            window.setInterval(() => {
                this.now = Math.trunc((new Date()).getTime() / 1000)
            }, 1000)
        },

        computed: {
            calculatedDate () {
                this.event = Math.trunc(Date.parse(this.event) / 1000)
                return this.event
            },
            seconds () {
                return (this.calculatedDate - this.now) % 60
            },
            minutes () {
                return Math.trunc((this.calculatedDate - this.now) / 60) % 60
            },
            hours () {
                return Math.trunc((this.calculatedDate - this.now) / 60 / 60) % 24
            },
            days () {
                return Math.trunc((this.calculatedDate - this.now) / 60 / 60 / 24)
            },
        },
    };
</script>


