<template>
    <f7-page>
        <f7-navbar sliding title="Tally" back-link="Back" color="ldnpk_theme">
            <f7-nav-right><f7-link icon="icon-bars" popup-open="#help-popup">Help</f7-link></f7-nav-right>
        </f7-navbar>

        <f7-list class="counters">
            <counter v-for="n in range" :key="n" class="color-ldnpk_theme"></counter>
        </f7-list>

        <div class="block">
            <f7-button center big fill color="pink" @click="addCounter">+</f7-button>
        </div>

        <f7-popup id="help-popup">
            <f7-view class="popup-view">
                <f7-page>
                    <div class="navbar">
                        <div class="navbar-inner">
                            <div class="left margin-left"><i class="f7-icons ios-only link-color">help</i><i class="material-icons md-only">help_outline</i></div>
                            <div class="title">Tally</div>
                            <div class="right"><a class="link popup-close" href="#">Close</a></div>
                        </div>
                    </div>
                    <div class="page-content block">
                        <h1>Tally Help</h1>
                        <p>A simple, labeled, counter to track a number of different things.</p>
                        <p>You can type a meaningful label into the text box to make the counter easier to identify.</p>
                        <p>The minus and plus buttons will add or remove a count of 1 to the existing score.</p>
                        <p>To add multiple counters, click on the big + button underneath.</p>
                        <p>Reset the tool by exiting to the tool menu and starting again.</p>
                        <hr/>
                        <p>An example of use would be to keep track of multiple students and their tally of an exercise.</p>
                    </div>
                </f7-page>
            </f7-view>
        </f7-popup>


    </f7-page>
</template>


<script>
    import F7List from "../../node_modules/framework7-vue/src/components/list";
    import F7ListItem from "../../node_modules/framework7-vue/src/components/list-item";
    import F7Button from "../../node_modules/framework7-vue/src/components/button";

    import counter from "./tool_counter.vue";

    export default {
        components: {F7Button, counter},
        name: 'Counters',

        data() {
            return {
                range:1
            }
        },

        methods: {

            addCounter: function(){
                this.range += 1;
            },
        }
    };
</script>


