<template>
    <f7-page>
        <f7-navbar sliding title="Countdown" back-link="Back" color="ldnpk_theme">
            <f7-nav-right><f7-link icon="icon-bars" popup-open="#help-popup">Help</f7-link></f7-nav-right>
        </f7-navbar>

        <div class="list links-list">
            <countdown :date="datetime" v-for="n in range" :key="n" class="color-ldnpk_theme"></countdown>
        </div>

        <div class="block">
            <f7-button center big fill color="pink" @click="addCountdown">+</f7-button>
        </div>

        <f7-popup id="help-popup">
            <f7-view class="popup-view">
                <f7-page>
                    <div class="navbar">
                        <div class="navbar-inner">
                            <div class="left margin-left"><i class="f7-icons ios-only link-color">help</i><i class="material-icons md-only">help_outline</i></div>
                            <div class="title">Countdown</div>
                            <div class="right"><a class="link popup-close" href="#">Close</a></div>
                        </div>
                    </div>
                    <div class="page-content block">
                        <h1>Countdown Help</h1>
                        <p>The countdown starts immediately as you start the tool. You are able to add multiple countdowns,
                            each starting from the moment the add + button is pressed.</p>
                        <hr/>
                        <p>An effective way to keep track of time on class stations or doing a particular exercise</p>
                    </div>
                </f7-page>
            </f7-view>
        </f7-popup>

    </f7-page>
</template>


<script>
    import countdown from "./tool_countdown.vue";

    export default {
        components: {countdown},
        name: 'countdowns',

        data() {
            return {
                range:1,
                datetime: new Date(),
            }
        },

        methods: {
            addCountdown: function(){
                this.datetime = new Date();
                this.range += 1;
            },
        }
    };
</script>


