<template>
    <f7-page>
        <f7-navbar title="Settings" back-link="Back" ></f7-navbar>
        <f7-block class="color-ldnpk_theme">
            <h1>Settings</h1>
        </f7-block>

        <f7-list class="ldnpk_list_items">
            <f7-list-item v-if="!this.coach" class="color-ldnpk_theme" @click="openlogin"><f7-link>Coach Login</f7-link></f7-list-item>
            <f7-list-item v-else class="color-ldnpk_theme" @click="openLogout"><f7-link>Coach Logout</f7-link></f7-list-item>
            <f7-list-item class="color-ldnpk_theme" @click="openAppVersion"><f7-link>App Version</f7-link></f7-list-item>
            <f7-list-item class="color-ldnpk_theme" ><f7-link popup-open="#help-popup">Changelog</f7-link></f7-list-item>
        </f7-list>

        <f7-popup id="help-popup">
            <f7-view class="popup-view">
                <f7-page>
                    <div class="navbar">
                        <div class="navbar-inner">
                            <div class="left margin-left"><i class="f7-icons ios-only link-color">help</i><i class="material-icons md-only">help_outline</i></div>
                            <div class="title">Changelog</div>
                            <div class="right"><a class="link popup-close" href="#">Close</a></div>
                        </div>
                    </div>
                    <div class="page-content block">
                        <h1>v1.0.0</h1>
                        <p>Initial Release.</p>
                    </div>
                </f7-page>
            </f7-view>
        </f7-popup>

        <!--user: {{ hashuser }}<br/>-->
        <!--code: {{ hashcode }}-->

    </f7-page>
</template>
<script>
    import F7ListItem from "../../node_modules/framework7-vue/src/components/list-item";
    export default {

        components: {F7ListItem},
        name: 'Settings',

        data() {
            return {
                hashuser: 102828657,
                hashcode: -336192238,
//                hashuser: this.hashme(""),
//                hashcode: this.hashme(""),
                inputbox: "none",
                coach: null,
            };
        },

        mounted: function(){
            this.coach = window.localStorage.getItem('coach');
        },

        computed: {
            inputcode: function() {
                return this.hashme(this.inputbox);
            },
        },

        methods: {
            hashme: function(str){
                /* Simple hash function. */
                var hash = 0;
                var i = 0;
                if (str.length == 0) return hash;


                for (i = 0; i < str.length; i++) {
                    var char = str.charCodeAt(i);
                    hash = ((hash<<5)-hash)+char;
                    hash = hash & hash; // Convert to 32bit integer
                }

                return hash;
            },

            openlogin: function(){
                const app = this.$f7;
                var hashcode = this.hashcode;
                var root = this.$root.$data;
                var hashit = this.hashme;
                var storage = window.localStorage;

                app.dialog.login('Enter your username and password', function (username, password) {
                    if (hashit(password) == hashcode){
                        storage.setItem('coach', true);
                        app.dialog.alert('Thank you, Coach!<br/>Coach functions unlocked. ');
                        app.views.main.router.back('/',{'ignoreCache': true, 'force': true});
                    } else {
                        storage.removeItem('coach');
                        app.dialog.alert('Incorrect password.');
                        app.views.main.router.back('/',{'ignoreCache': true, 'force': true});
                    }

                });
            },

            openLogout: function(){
                window.localStorage.removeItem('coach');
                this.$f7.views.main.router.back('/',{'ignoreCache': true, 'force': true});

            },

            openAppVersion: function(){
                this.$f7.dialog.alert('v1.0.0', 'App Version');
            }
        }


    };
</script>


