<template>
        <li>
            <div class="item-content">
                <div class="item-inner">
                    <div class="item-title">

                        <input value="Person" class="ldnpk_input"></input>
                    </div>
                    <div class="item-after">
                        <div>
                            <div class="item-header"><span id="time" v-html="time" class="ldnpk_timers"></span></div>
                            <button class="button resetbutton" @click="reset"><f7-icon f7="reload_round_fill"></f7-icon></button>
                            <button class="button playbutton" v-if="state=='paused'" @click="resume"><f7-icon f7="play_fill"></f7-icon></button>
                            <button class="button playbutton" v-if="state=='started'" @click="pause" ><f7-icon f7="pause_fill"></f7-icon></button>
                        </div>
                    </div>
                </div>
            </div>
        </li>
</template>


<script>
    import F7Button from "../../node_modules/framework7-vue/src/components/button";

    export default {
        components: {F7Button},
        name: 'stopwatch',

        data() {
            return {
                state: "started",
                startTime: Date.now(),
                currentTime: Date.now(),
                interval: null,
            }
        },

        mounted: function() {
            this.interval = setInterval(this.updateCurrentTime, 1000);
            this.pause();

        },

        destroyed: function() {
            clearInterval(this.interval)
        },

        computed: {
            time: function() {
                return this.hours + ':' + this.minutes + ':' + this.seconds;
            },
            milliseconds: function() {
                return this.$data.currentTime - this.$data.startTime;
            },
            hours: function() {
                var lapsed = this.milliseconds;
                var hrs = Math.floor((lapsed / 1000 / 60 / 60));
                return hrs >= 10 ? hrs : '0' + hrs;
            },
            minutes: function() {
                var lapsed = this.milliseconds;
                var min = Math.floor((lapsed / 1000 / 60) % 60);
                return min >= 10 ? min : '0' + min;
            },
            seconds: function() {
                var lapsed = this.milliseconds;
                var sec = Math.ceil((lapsed / 1000) % 60);
                return sec >= 10 ? sec : '0' + sec;
            },
        },

        methods: {
            reset: function() {
                this.$data.startTime = Date.now();
                this.$data.currentTime = Date.now();
            },
            pause: function() {
                this.$data.state = "paused";
            },
            resume: function() {
                this.$data.state = "started";
            },
            updateCurrentTime: function() {
                if (this.$data.state == "started") {
                    this.$data.currentTime = Date.now();
                }
            },

        }
    };
</script>


